import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Line from './progress-line';
import Circle from './progress-circle';

/**
 * Progress
 */
export default class Progress extends Component {
  static propTypes = {
    prefix: PropTypes.string,
    /**
     * 
     */
    shape: PropTypes.oneOf(['circle', 'line']),
    /**
     * 
     */
    size: PropTypes.oneOf(['small', 'medium', 'large']),
    /**
     * 
     */
    percent: PropTypes.number,
    /**
     * , : color > progressive > state
     */
    state: PropTypes.oneOf(['normal', 'success', 'warning', 'error']),
    /**
     * , : color > progressive > state
     */
    progressive: PropTypes.bool,
    /**
     *  Border Line Progress)
     */
    hasBorder: PropTypes.bool,
    /**
     * 
     * @param {Number} percent 
     * @param {Object} option 
     * @property {Boolean} option.rtl rtl 
     * @return {ReactNode} 
     */
    textRender: PropTypes.func,
    /**
     * , : color > progressive > state
     */
    color: PropTypes.string,
    /**
     * 
     */
    backgroundColor: PropTypes.string,
    rtl: PropTypes.bool,
  };

  static defaultProps = {
    prefix: 'next-',
    shape: 'line',
    state: 'normal',
    size: 'medium',
    percent: 0,
    progressive: false,
    hasBorder: false,
    textRender: percent => `${Math.floor(percent)}%`,
  };

  static contextTypes = {
    prefix: PropTypes.string,
  };

  render() {
    const { shape, hasBorder, ...others } = this.props;
    return shape === 'circle' ? <Circle {...others} /> : <Line {...others} hasBorder={hasBorder} />;
  }
}
