import React, { Component, Children } from 'react';
import PropTypes from 'prop-types';
import Overlay from '../overlay';
import { func } from '../util';

const { noop, makeChain } = func;
const { Popup } = Overlay;

/**
 * Dropdown
 * @description  Popup  API
 */
export default class Dropdown extends Component {
  static propTypes = {
    prefix: PropTypes.string,
    pure: PropTypes.bool,
    rtl: PropTypes.bool,
    className: PropTypes.string,
    /**
     * 
     */
    children: PropTypes.node,
    /**
     * 
     */
    visible: PropTypes.bool,
    /**
     * 
     */
    defaultVisible: PropTypes.bool,
    /**
     * 
     * @param {Boolean} visible 
     * @param {String} type  fromContent Dropdown fromTrigger trigger docClick document
     */
    onVisibleChange: PropTypes.func,
    /**
     * 
     */
    trigger: PropTypes.node,
    /**
     *  'click''hover' ['hover', 'click']
     */
    triggerType: PropTypes.oneOfType([PropTypes.string, PropTypes.array]),
    /**
     * 
     */
    disabled: PropTypes.bool,
    /**
     * ,  Overlay 
     */
    align: PropTypes.string,
    /**
     * trigger, [hoz, ver],  left / top 
     * e.g. [100, 100] (RTL ) 100px
     */
    offset: PropTypes.array,
    /**
     *  triggerType  hover 
     */
    delay: PropTypes.number,
    /**
     * 
     */
    autoFocus: PropTypes.bool,
    /**
     * 
     */
    hasMask: PropTypes.bool,
    /**
     * 
     */
    cache: PropTypes.bool,
    /**
     *  { in: 'enter-class', out: 'leave-class' }  false
     * @default { in: 'expandInDown', out: 'expandOutUp' }
     */
    animation: PropTypes.oneOfType([PropTypes.object, PropTypes.bool]),
  };

  static defaultProps = {
    prefix: 'next-',
    pure: false,
    defaultVisible: false,
    onVisibleChange: noop,
    triggerType: 'hover',
    disabled: false,
    align: 'tl bl',
    offset: [0, 4],
    delay: 200,
    hasMask: false,
    cache: false,
  };

  constructor(props) {
    super(props);

    this.state = {
      visible: 'visible' in props ? props.visible : props.defaultVisible || false,
      autoFocus: 'autoFocus' in props ? props.autoFocus : false,
    };
    this.popup = null;
  }

  open = () => {
    this.onVisibleChange(true, 'fromComponentMethod');
  };

  close = () => {
    this.onVisibleChange(false, 'fromComponentMethod');
  };

  toggle = () => {
    if (this.getVisible()) {
      this.close();
    } else {
      this.open();
    }
  };

  getVisible = (props = this.props) => {
    return 'visible' in props ? props.visible : this.state.visible;
  };

  // overlay
  getOverlay = () => {
    try {
      return this.popup.getInstance().getOverlay();
    } catch (err) {
      return null;
    }
  };

  onMenuClick = () => {
    this.onVisibleChange(false, 'fromContent');
  };

  onVisibleChange = (visible, from) => {
    this.setState({ visible });

    this.props.onVisibleChange(visible, from);
  };

  onTriggerKeyDown = () => {
    let autoFocus = true;

    if ('autoFocus' in this.props) {
      autoFocus = this.props.autoFocus;
    }

    this.setState({
      autoFocus,
    });
  };

  render() {
    let child = Children.only(this.props.children);
    if (typeof child.type === 'function' && child.type.isNextMenu) {
      child = React.cloneElement(child, {
        onItemClick: makeChain(this.onMenuClick, child.props.onItemClick),
      });
    }

    const { trigger, rtl } = this.props;
    const newTrigger = React.cloneElement(trigger, {
      onKeyDown: makeChain(this.onTriggerKeyDown, trigger.props.onKeyDown),
    });

    return (
      <Popup
        animation={{ in: 'fadeInDown', out: 'fadeOutUp' }}
        {...this.props}
        ref={popup => {
          this.popup = popup;
        }}
        rtl={rtl}
        autoFocus={this.state.autoFocus}
        trigger={newTrigger}
        visible={this.getVisible()}
        onVisibleChange={this.onVisibleChange}
      >
        {child}
      </Popup>
    );
  }
}
