import React, { Component } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import ConfigProvider from '../config-provider';
import Menu from '../menu';

/**
 * Nav
 * @description  `Menu`  `Menu` 
 */
class Nav extends Component {
 static propTypes = {
   prefix: PropTypes.string,
   pure: PropTypes.bool,
   rtl: PropTypes.bool,
   className: PropTypes.string,
   style: PropTypes.object,
   /**
    * 
    */
   children: PropTypes.node,
   /**
    * 
    * @enumdesc , , , 
    */
   type: PropTypes.oneOf(['normal', 'primary', 'secondary', 'line']),
   /**
    * 
    * @enumdesc , 
    */
   direction: PropTypes.oneOf(['hoz', 'ver']),
   /**
    *  items  footer  direction  'hoz'  header 
    */
   hozAlign: PropTypes.oneOf(['left', 'right']),
   /**
    *  active 
    * @enumdesc , , , , 
    * @default  direction  'hoz'  'bottom' direction  'ver'  'left'
    */
   activeDirection: PropTypes.oneOf([null, 'top', 'bottom', 'left', 'right']),
   /**
    * 
    * @eumdesc , 
    */
   mode: PropTypes.oneOf(['inline', 'popup']),
   /**
    * 
    */
   triggerType: PropTypes.oneOf(['click', 'hover']),
   /**
    * 
    */
   inlineIndent: PropTypes.number,
   /**
    *  mode  'inline'  openMode  'multiple' 
    */
   defaultOpenAll: PropTypes.bool,
   /**
    *  mode  inline 
    * @eumdesc , 
    */
   openMode: PropTypes.oneOf(['single', 'multiple']),
   /**
    *  key 
    */
   selectedKeys: PropTypes.oneOfType([PropTypes.string, PropTypes.array]),
   /**
    *  key 
    */
   defaultSelectedKeys: PropTypes.oneOfType([PropTypes.string, PropTypes.array]),
   /**
    * 
    * @param {Array} selectedKeys  key
    * @param {Object} item 
    * @param {Object} extra 
    * @param {Boolean} extra.select 
    * @param {Array} extra.key  key
    * @param {Object} extra.label 
    * @param {Array} extra.keyPath  key 
    */
   onSelect: PropTypes.func,
   /**
    *  follow 
    * @eumdesc Item , Nav 
    */
   popupAlign: PropTypes.oneOf(['follow', 'outside']),
   /**
    * 
    */
   popupClassName: PropTypes.string,
   /**
    * 
    */
   iconOnly: PropTypes.bool,
   /**
    *  iconOnly=true 
    */
   hasArrow: PropTypes.bool,
   /**
    * 
    */
   noBg: PropTypes.bool,
   /**
    *  ToolTips  iconOnly=true 
    */
   hasTooltip: PropTypes.bool,
   /**
    * 
    */
   header: PropTypes.node,
   /**
    * 
    */
   footer: PropTypes.node,
   /**
    * Layoutborderbox-shadow`<Nav style={{lineHeight: '100px'}}>` 
    */
   embeddable: PropTypes.bool,
   popupProps: PropTypes.object,
 };

 static defaultProps = {
   prefix: 'next-',
   pure: false,
   type: 'normal',
   direction: 'ver',
   hozAlign: 'left',
   mode: 'inline',
   triggerType: 'click',
   inlineIndent: 20,
   defaultOpenAll: false,
   openMode: 'multiple',
   defaultSelectedKeys: [],
   popupAlign: 'follow',
   hasTooltip: false,
   embeddable: false,
   hasArrow: true,
   noBg: false,
   popupProps: {},
 };

 static childContextTypes = {
   prefix: PropTypes.string,
   mode: PropTypes.string,
   iconOnly: PropTypes.bool,
   hasTooltip: PropTypes.bool,
   hasArrow: PropTypes.bool,
   noBg: PropTypes.bool,
 };

 getChildContext() {
   const { prefix, direction, mode, iconOnly, hasTooltip, hasArrow } = this.props;

   return {
     prefix,
     mode: direction === 'hoz' ? 'popup' : mode,
     iconOnly,
     hasTooltip,
     hasArrow,
   };
 }

 getMenuRef = ref => {
   this.menu = ref;
 };

 render() {
   // eslint-disable-next-line
   const {
     prefix,
     className,
     style,
     children,
     type,
     direction,
     activeDirection,
     mode,
     triggerType,
     inlineIndent,
     openMode,
     popupAlign,
     popupClassName,
     iconOnly,
     hasArrow,
     noBg,
     hasTooltip,
     embeddable,
     popupProps,
     rtl,
     ...others
   } = this.props;

   let realActiveDirection = activeDirection;
   if (
     realActiveDirection &&
     ((direction === 'hoz' &&
       (realActiveDirection === 'left' || realActiveDirection === 'right')) ||
       (direction === 'ver' &&
         (realActiveDirection === 'top' || realActiveDirection === 'bottom')))
   ) {
     realActiveDirection = null;
   }

   if (!iconOnly && realActiveDirection === undefined) {
     realActiveDirection = direction === 'hoz' ? 'bottom' : type === 'line' ? 'right' : 'left';
   }
   const cls = classNames({
     [`${prefix}nav`]: true,
     [`${prefix}${type}`]: type,
     [`${prefix}active`]: realActiveDirection,
     [`${prefix}${realActiveDirection}`]: realActiveDirection,
     [`${prefix}icon-only`]: iconOnly,
     [`${prefix}no-arrow`]: !hasArrow,
     [`${prefix}no-bg`]: noBg,
     [`${prefix}nav-embeddable`]: embeddable,
     [className]: !!className,
   });
   const newStyle = iconOnly ? { ...style, width: '60px' } : style;

   const props = {
     prefix,
     direction,
     openMode,
     triggerType,
     mode: direction === 'hoz' ? 'popup' : mode,
     popupAlign: direction === 'hoz' ? 'follow' : popupAlign,
     inlineIndent: iconOnly ? 0 : inlineIndent,
     hasSelectedIcon: false,
     popupAutoWidth: true,
     selectMode: 'single',
     itemClassName: `${prefix}nav-item`,
     popupClassName: classNames({
       [cls.replace(`${prefix}icon-only`, '').replace(`${prefix}nav-embeddable`, '')]:
         mode === 'popup',
       [`${prefix}icon-only`]: iconOnly && mode === 'inline',
       [popupClassName]: !!popupClassName,
     }),
     popupProps: popupItemProps => {
       return {
         offset: direction === 'hoz' && popupItemProps.level === 1 ? [0, 2] : [-2, 0],
         ...popupProps,
       };
     },
   };

   return (
     <Menu className={cls} style={newStyle} {...props} {...others} ref={this.getMenuRef}>
       {children}
     </Menu>
   );
 }
}

export default ConfigProvider.config(Nav);
