/* eslint-disable react/no-multi-comp */

import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames';
import shallowElementEquals from 'shallow-element-equals';
import Loading from '../loading';
import zhCN from '../locale/zh-cn';
import { log, obj } from '../util';
import BodyComponent from './base/body';
import HeaderComponent from './base/header';
import WrapperComponent from './base/wrapper';
import RowComponent from './base/row';
import CellComponent from './base/cell';
import FilterComponent from './base/filter';
import SortComponent from './base/sort';
import Column from './column';
import ColumnGroup from './column-group';
import guid from '../util/guid';
import AutoWidth from './base/auto-width';

const { Children } = React;
const noop = () => {};

class ColGroup extends React.Component {
  componentDidMount() {
    const { table } = this.props;
    table.AutoWidth.bindColGroup(this);
  }

  componentDidUpdate() {
    const { table } = this.props;
    table.AutoWidth.bindColGroup(this);
  }

  render() {
    const { flatChildren, table } = this.props;

    const cols = flatChildren.map((col, index) => {
      const { width, columnId } = col;
      let style = {};
      let w = width;

      if (width === 'auto' && table.AutoWidth.enable) {
        w = table.AutoWidth.getBestWidth(columnId);
      }

      if (w) {
        style = { width: w };
      }

      return <col style={style} key={index} />;
    });

    return <colgroup key="table-colgroup">{cols}</colgroup>;
  }
}

/** Table */
export default class Table extends React.Component {
  static displayName = 'Table';

  static Column = Column;

  static ColumnGroup = ColumnGroup;

  static Header = HeaderComponent;

  static Body = BodyComponent;

  static Wrapper = WrapperComponent;

  static Row = RowComponent;

  static Cell = CellComponent;

  static Filter = FilterComponent;

  static Sort = SortComponent;

  static propTypes = {
    /**
     * 
     */
    prefix: PropTypes.string,
    pure: PropTypes.bool,
    rtl: PropTypes.bool,
    /**
     * 
     */
    className: PropTypes.string,
    /**
     * 
     */
    style: PropTypes.object,
    /**
     * 
     */
    dataSource: PropTypes.array,
    entireDataSource: PropTypes.array,
    /**
     * 
     * @param {Object} record 
     * @param {Number} index 
     * @param {Event} e DOM
     */
    onRowClick: PropTypes.func,
    /**
     * 
     * @param {Object} record 
     * @param {Number} index 
     * @param {Event} e DOM
     */
    onRowMouseEnter: PropTypes.func,
    /**
     * 
     * @param {Object} record 
     * @param {Number} index 
     * @param {Event} e DOM
     */
    onRowMouseLeave: PropTypes.func,
    /**
     * 
     * @param {String} dataIndex 
     * @param {String} order , `desc``asc`
     */
    onSort: PropTypes.func,
    /**
     * 
     * @param {Object} filterParams 
     */
    onFilter: PropTypes.func,
    /**
     * 
     * @param {String} dataIndex 
     * @param {Number} value 
     */
    onResizeChange: PropTypes.func,
    /**
     * 
     * @param {Object} record 
     * @param {Number} index 
     * @returns {Object} 
     */
    rowProps: PropTypes.func,
    /**
     * 
     * @param {Number} rowIndex 
     * @param {Number} colIndex 
     * @param {String} dataIndex 
     * @param {Object} record 
     * @returns {Object} td
     */
    cellProps: PropTypes.func,
    /**
     * 
     */
    hasBorder: PropTypes.bool,
    /**
     * 
     */
    hasHeader: PropTypes.bool,
    /**
     * 
     */
    isZebra: PropTypes.bool,
    /**
     * 
     */
    loading: PropTypes.bool,
    /**
     *  Loading 
     *  props,  loadingComponent={props => <Loading {...props}/>}
     * @param {Object} props key
     */
    loadingComponent: PropTypes.func,
    /**
     * keys,, {dataIndex: {selectedKeys:[]}}
     * :
     * dataIndexidkeyone
     * `<Table filterParams={{id: {selectedKeys: ['one']}}}/>`
     */
    filterParams: PropTypes.object,
    /**
     * ,,{dataIndex: 'asc'}
     */
    sort: PropTypes.object,
    /**
     * : `{desc: <Icon style={{top: '6px', left: '4px'}} type={'chevron-down'} size="small" />, asc: <Icon style={{top: '-6px', left: '4px'}} type={'chevron-up'} size="small" />}`
     */
    sortIcons: PropTypes.object,
    /**
     * 
     * @property {String} ok 
     * @property {String} reset 
     * @property {String} empty  table
     * @property {String} asc 
     * @property {String} desc 
     * @property {String} expanded 
     * @property {String} folded 
     * @property {String} filter 
     * @property {String} selectAll header
     */
    locale: PropTypes.object,
    components: PropTypes.object,
    columns: PropTypes.array,
    /**
     * 
     */
    emptyContent: PropTypes.node,
    /**
     * dataSource
     */
    primaryKey: PropTypes.string,
    lockType: PropTypes.oneOf(['left', 'right']),
    wrapperContent: PropTypes.any,
    refs: PropTypes.object,
    /**
     * 
     * @param {Object} record 
     * @param {Number} index 
     * @returns {Element} 
     */
    expandedRowRender: PropTypes.func,
    /**
     * 
     */
    expandedRowIndent: PropTypes.array,
    /**
     * Tree, 
     */
    openRowKeys: PropTypes.array,
    /**
     * +
     */
    hasExpandedRowCtrl: PropTypes.bool,
    /**
     * 
     */
    getExpandedColProps: PropTypes.func,
    /**
     * Tree
     * @param {Array} openRowKeys key
     * @param {String} currentRowKey key
     * @param {Boolean} expanded 
     * @param {Object} currentRecord 
     */
    onRowOpen: PropTypes.func,
    /**
     * 
     * @param {Object} record 
     * @param {Number} index 
     * @param {Event} e DOM
     */
    onExpandedRowClick: PropTypes.func,
    /**
     * maxBodyHeightmaxBodyHeight
     */
    fixedHeader: PropTypes.bool,
    /**
     * ,`fixedHeader``true`,
     */
    maxBodyHeight: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
    /**
     * 
     * @property {Function} getProps `Function(record, index)=>Object` selection
     * @property {Function} onChange `Function(selectedRowKeys:Array, records:Array)` **:** recordsdataSourceselectedRowKeys
     * @property {Function} onSelect `Function(selected:Boolean, record:Object, records:Array)` /
     * @property {Function} onSelectAll `Function(selected:Boolean, records:Array)` /
     * @property {Array} selectedRowKeys ,rowSelection,primaryKey
     * @property {String} mode selection, `single`, `multiple``multiple`
     * @property {Function} columnProps `Function()=>Object`  props`Table.Column` 
     * @property {Function} titleProps `Function()=>Object`  props `multiple` 
     * @property {Function} titleAddons `Function()=>Node`  `single` `multiple` 
     */
    rowSelection: PropTypes.object,
    /**
     * sticky
     */
    stickyHeader: PropTypes.bool,
    /**
     * 
     */
    offsetTop: PropTypes.number,
    /**
     * affix
     */
    affixProps: PropTypes.object,
    /**
     * tree isTreetrue
     */
    indent: PropTypes.number,
    /**
     * Tabletree, childrentree table
     */
    isTree: PropTypes.bool,
    /**
     * 
     */
    useVirtual: PropTypes.bool,
    /**
     * 
     */
    rowHeight: PropTypes.oneOfType([PropTypes.number, PropTypes.func]),
    /**
     * 
     */
    onBodyScroll: PropTypes.func,
    /**
     * getExpandedColProps() / rowProps() / expandedRowRender()  index () 01,2,3,4...index(0,2,4,6... / 1,3,5,7...)
     */
    expandedIndexSimulate: PropTypes.bool,
  };

  static defaultProps = {
    dataSource: [],
    onRowClick: noop,
    onRowMouseEnter: noop,
    onRowMouseLeave: noop,
    onSort: noop,
    onFilter: noop,
    onResizeChange: noop,
    rowProps: noop,
    cellProps: noop,
    prefix: 'next-',
    hasBorder: true,
    hasHeader: true,
    isZebra: false,
    loading: false,
    expandedIndexSimulate: false,
    primaryKey: 'id',
    components: {},
    locale: zhCN.Table,
  };

  static childContextTypes = {
    notRenderCellIndex: PropTypes.array,
    lockType: PropTypes.oneOf(['left', 'right']),
  };

  static contextTypes = {
    getTableInstance: PropTypes.func,
    getTableInstanceForVirtual: PropTypes.func,
  };

  constructor(props, context) {
    super(props, context);
    const { getTableInstance, getTableInstanceForVirtual } = this.context;
    getTableInstance && getTableInstance(props.lockType, this);
    getTableInstanceForVirtual && getTableInstanceForVirtual(props.lockType, this);
    this.notRenderCellIndex = [];
    this.refColGroup = null;
    this.AutoWidth = new AutoWidth({ padding: 15, table: this });
  }

  state = {
    sort: this.props.sort || {},
  };

  getChildContext() {
    return {
      notRenderCellIndex: this.notRenderCellIndex,
      lockType: this.props.lockType,
    };
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    if (typeof this.props.sort !== 'undefined') {
      this.setState({
        sort: nextProps.sort,
      });
    }
  }

  shouldComponentUpdate(nextProps, nextState, nextContext) {
    if (nextProps.pure) {
      const isEqual =
        shallowElementEquals(nextProps, this.props) &&
        obj.shallowEqual(nextState, this.state) &&
        obj.shallowEqual(nextContext, this.context);
      return !isEqual;
    }

    return true;
  }

  UNSAFE_componentWillUpdate() {
    this.notRenderCellIndex = [];
  }

  componentDidMount() {
    if (this.AutoWidth && this.AutoWidth.enable) {
      this.AutoWidth.calcAndUpdate();
    }
  }

  componentDidUpdate() {
    if (this.AutoWidth && this.AutoWidth.enable) {
      this.AutoWidth.calcAndUpdate();
    }
  }

  normalizeChildrenState(props) {
    let { columns } = props;
    if (props.children) {
      columns = this.normalizeChildren(props);
    }
    return this.fetchInfoFromBinaryChildren(columns);
  }

  // Reactprops
  normalizeChildren(props) {
    let { columns } = props;
    const getChildren = children => {
      const ret = [];
      Children.forEach(children, child => {
        if (child) {
          const props = { ...child.props };

          if (
            !(
              child &&
              typeof child.type === 'function' &&
              (child.type._typeMark === 'column' || child.type._typeMark === 'columnGroup')
            )
          ) {
            log.warning('Use <Table.Column/>, <Table.ColumnGroup/> as child.');
          }
          ret.push(props);
          if (child.props.children) {
            props.children = getChildren(child.props.children);
          }
        }
      });
      return ret;
    };
    if (props.children) {
      columns = getChildren(props.children);
    }
    return columns;
  }

  fetchInfoFromBinaryChildren(children) {
    let hasGroupHeader = false;
    const flatChildren = [];
    const groupChildren = [];
    const getChildren = (propsChildren, level) => {
      groupChildren[level] = groupChildren[level] || [];
      propsChildren.forEach(child => {
        if (child.children) {
          hasGroupHeader = true;
          getChildren(child.children, level + 1);
        } else {
          child.columnId = guid('cloumn-');

          if (this.AutoWidth.enable) {
            this.AutoWidth.analyzeColDefine(child);
          }

          flatChildren.push(child);
        }
        groupChildren[level].push(child);
      });
    };
    const getColSpan = (children, colSpan) => {
      colSpan = colSpan || 0;
      children.forEach(child => {
        if (child.children) {
          colSpan = getColSpan(child.children, colSpan);
        } else {
          colSpan += 1;
        }
      });
      return colSpan;
    };

    getChildren(children, 0);

    groupChildren.forEach((groupChild, i) => {
      groupChild.forEach((child, j) => {
        let colSpan;
        const { children } = child;

        if (children) {
          colSpan = getColSpan(children);
          child.colSpan = colSpan;
          groupChildren[i][j] = child;
        }
      });
    });

    return {
      flatChildren,
      groupChildren,
      hasGroupHeader,
    };
  }

  renderColGroup = (flatChildren) => {
    return <ColGroup flatChildren={flatChildren} table={this} />;
  }

  onSort = (dataIndex, order, sort) => {
    if (typeof this.props.sort === 'undefined') {
      this.setState(
        {
          sort,
        },
        () => {
          this.props.onSort(dataIndex, order, sort);
        },
      );
    } else {
      this.props.onSort(dataIndex, order, sort);
    }
  };

  onFilter = filterParams => {
    this.props.onFilter(filterParams);
  };

  onResizeChange = (dataIndex, value) => {
    this.props.onResizeChange(dataIndex, value);
  };

  // 
  renderTable(groupChildren, flatChildren) {
    if (flatChildren.length || (!flatChildren.length && !this.props.lockType)) {
      const {
        hasHeader,
        components,
        prefix,
        wrapperContent,
        filterParams,
        locale,
        dataSource,
        emptyContent,
        loading,
        primaryKey,
        cellProps,
        rowProps,
        onRowClick,
        onRowMouseEnter,
        onRowMouseLeave,
        expandedIndexSimulate,
        pure,
        rtl,
        sortIcons,
      } = this.props;
      const { sort } = this.state;

      const {
        Header = HeaderComponent,
        Wrapper = WrapperComponent,
        Body = BodyComponent,
      } = components;

      const colGroup = this.renderColGroup(flatChildren);

      return (
        <Wrapper colGroup={colGroup} ref={this.getWrapperRef} prefix={prefix}>
          {hasHeader ? (
            <Header
              prefix={prefix}
              rtl={rtl}
              pure={pure}
              affixRef={this.getAffixRef}
              colGroup={colGroup}
              className={`${prefix}table-header`}
              filterParams={filterParams}
              columns={groupChildren}
              locale={locale}
              headerCellRef={this.getHeaderCellRef}
              components={components}
              onFilter={this.onFilter}
              sort={sort}
              onResizeChange={this.onResizeChange}
              onSort={this.onSort}
              sortIcons={sortIcons}
              table={this}
            />
          ) : null}
          <Body
            prefix={prefix}
            rtl={rtl}
            pure={pure}
            colGroup={colGroup}
            className={`${prefix}table-body`}
            components={components}
            loading={loading}
            emptyContent={emptyContent}
            getCellProps={cellProps}
            primaryKey={primaryKey}
            getRowProps={rowProps}
            columns={flatChildren}
            rowRef={this.getRowRef}
            cellRef={this.getCellRef}
            onRowClick={onRowClick}
            expandedIndexSimulate={expandedIndexSimulate}
            onRowMouseEnter={onRowMouseEnter}
            onRowMouseLeave={onRowMouseLeave}
            dataSource={dataSource}
            locale={locale}
            table={this}
          />
          {wrapperContent}
        </Wrapper>
      );
    } else {
      return null;
    }
  }

  getWrapperRef = wrapper => {
    if (!wrapper) {
      return this.wrapper;
    }
    this.wrapper = wrapper;
  };

  getAffixRef = affixRef => {
    if (!affixRef) {
      return this.affixRef;
    }
    this.affixRef = affixRef;
  };

  getHeaderCellRef = (i, j, cell) => {
    const cellRef = `header_cell_${i}_${j}`;
    if (!cell) {
      return this[cellRef];
    }
    this[cellRef] = cell;
  };

  getRowRef = (i, row) => {
    const rowRef = `row_${i}`;
    if (!row) {
      return this[rowRef];
    }
    this[rowRef] = row;
  };

  getCellRef = (i, j, cell) => {
    const cellRef = `cell_${i}_${j}`;
    if (!cell) {
      return this[cellRef];
    }
    this[cellRef] = cell;
  };

  render() {
    if (this.AutoWidth && this.AutoWidth.enable) {
      this.AutoWidth.init();
    }

    const ret = this.normalizeChildrenState(this.props);
    this.groupChildren = ret.groupChildren;
    this.flatChildren = ret.flatChildren;
    /* eslint-disable no-unused-vars, prefer-const */

    let table = this.renderTable(ret.groupChildren, ret.flatChildren);
    const {
      className,
      hasBorder,
      isZebra,
      loading,
      hasHeader,
      prefix,
      dataSource,
      entireDataSource,
      onSort,
      onResizeChange,
      onRowClick,
      onRowMouseEnter,
      onRowMouseLeave,
      onFilter,
      rowProps,
      cellProps,
      primaryKey,
      components,
      wrapperContent,
      lockType,
      locale,
      expandedIndexSimulate,
      refs,
      pure,
      rtl,
      emptyContent,
      filterParams,
      columns,
      sortIcons,
      loadingComponent: LoadingComponent = Loading,
      ...others
    } = this.props;
    const cls = classnames({
      [`${prefix}table`]: true,
      'only-bottom-border': !hasBorder,
      'no-header': !hasHeader,
      zebra: isZebra,
      [className]: className,
    });

    if (rtl) {
      others.dir = 'rtl';
    }
    const content = (
      <div className={cls} {...others}>
        {table}
      </div>
    );
    if (loading) {
      const loadingClassName = `${prefix}table-loading`;
      return <LoadingComponent className={loadingClassName}>{content}</LoadingComponent>;
    }

    return content;
  }
}
