import { Component, Children } from 'react';
import PropTypes from 'prop-types';
import getContextProps from './get-context-props';
import {
  config,
  initLocales,
  setLanguage,
  setLocale,
  setDirection,
  setScriptUrl,
  getLocale,
  getLanguage,
  getDirection,
} from './config';
import Consumer from './consumer';
import ErrorBoundary from './error-boundary';
import Cache from './cache';

const childContextCache = new Cache();

/**
 * ConfigProvider
 * @propsExtends false
 */
class ConfigProvider extends Component {
  static propTypes = {
    /**
     * 
     */
    prefix: PropTypes.string,
    /**
     *  displayName
     */
    locale: PropTypes.object,
    /**
     *  errorBoundary
     *  
     *
     * fallbackUI `Function(error?: {}, errorInfo?: {}) => Element` 
     * afterCatch `Function(error?: {}, errorInfo?: {})` , 
     */
    errorBoundary: PropTypes.oneOfType([PropTypes.bool, PropTypes.object]),
    /**
     *  Pure Render 
     */
    pure: PropTypes.bool,
    /**
     *  warning 
     */
    warning: PropTypes.bool,
    /**
     *  rtl 
     */
    rtl: PropTypes.bool,
    /**
     * 
     */
    children: PropTypes.element,
    /**
     * Icon symboljs
     */
    scriptUrl: PropTypes.string,
  };

  static defaultProps = {
    warning: true,
    errorBoundary: false,
  };

  static childContextTypes = {
    nextPrefix: PropTypes.string,
    nextLocale: PropTypes.object,
    nextPure: PropTypes.bool,
    nextRtl: PropTypes.bool,
    nextWarning: PropTypes.bool,
    nextErrorBoundary: PropTypes.oneOfType([PropTypes.bool, PropTypes.object]),
    nextScriptUrl: PropTypes.string,
  };

  /**
   *  ConfigProvider  HOC 
   * @param {Component} Component 
   * @param {Object} options 
   * @returns {Component} HOC
   */
  static config = (Component, options) => {
    return config(Component, options);
  };

  /**
   *  props  displayName childContext  preifx/locale/pure 
   * @param {Object} props  props
   * @param {String} displayName  displayName
   * @returns {Object}  context props
   */
  static getContextProps = (props, displayName) => {
    return getContextProps(props, childContextCache.root() || {}, displayName);
  };

  static initLocales = initLocales;

  static setLanguage = setLanguage;

  static setLocale = setLocale;

  static setDirection = setDirection;

  static setScriptUrl = setScriptUrl;

  static getLanguage = getLanguage;

  static getLocale = getLocale;

  static getDirection = getDirection;

  static Consumer = Consumer;

  static ErrorBoundary = ErrorBoundary;

  static getContext = () => {
    const { nextPrefix, nextLocale, nextPure, nextRtl, nextWarning, nextErrorBoundary, nextScriptUrl } =
      childContextCache.root() || {};

    return {
      prefix: nextPrefix,
      locale: nextLocale,
      pure: nextPure,
      rtl: nextRtl,
      warning: nextWarning,
      errorBoundary: nextErrorBoundary,
      scriptUrl: nextScriptUrl,
    };
  };

  constructor(...args) {
    super(...args);
    childContextCache.add(
      this,
      Object.assign({}, childContextCache.get(this, {}), this.getChildContext()),
    );
  }

  getChildContext() {
    const { prefix, locale, pure, warning, rtl, errorBoundary, scriptUrl } = this.props;

    return {
      nextPrefix: prefix,
      nextLocale: locale,
      nextPure: pure,
      nextRtl: rtl,
      nextWarning: warning,
      nextErrorBoundary: errorBoundary,
      nextScriptUrl: scriptUrl,
    };
  }

  componentDidMount() {
    this.setMomentLocale(this.props.locale);
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    if (this.props.locale !== nextProps.locale) {
      this.setMomentLocale(nextProps.locale);
    }
  }

  componentDidUpdate() {
    childContextCache.add(
      this,
      Object.assign({}, childContextCache.get(this, {}), this.getChildContext()),
    );
  }

  componentWillUnmount() {
    childContextCache.remove(this);
  }

  setMomentLocale(locale) {
    let moment;
    try {
      moment = require('moment');
    } catch (e) {
      // ignore
    }

    if (moment && locale) {
      moment.locale(locale.momentLocale);
    }
  }

  render() {
    return Children.only(this.props.children);
  }
}

export default ConfigProvider;
