import { isPromise } from './object';

/**
 * 
 */
export const noop = () => {};

/**
 * false
 */
export const prevent = () => false;

/**
 * N
 * @return {Function}     
 *  https://github.com/react-component/util/
 *
 * @example
 * func.makeChain(this.handleChange, this.props.onChange);
 */
export function makeChain(...fns) {
  if (fns.length === 1) {
    return fns[0];
  }

  return function chainedFunction(...args) {
    for (let i = 0, j = fns.length; i < j; i++) {
      if (fns[i] && fns[i].apply) {
        fns[i].apply(this, args);
      }
    }
  };
}

/**
 * 
 * reactconstructor
 * bind
 * @param  {Object} ctx 
 * @param  {Array<String>} fns 
 *
 * @example
 * func.bindCtx(this, ['handleClick', 'handleChange']);
 */
export function bindCtx(ctx, fns, ns) {
  if (typeof fns === 'string') {
    fns = [fns];
  }

  // ctx
  ns = ns || ctx;

  fns.forEach(fnName => {
    // 
    ns[fnName] = ns[fnName].bind(ctx);
  });
}

/**
 * 
 * @param  {*} ret            
 * @param  {Function} success false
 * @param  {Function} [failure=noop] false
 */
export function promiseCall(ret, success, failure = noop) {
  if (isPromise(ret)) {
    return ret
      .then(result => {
        success(result);
        return result;
      })
      .catch(e => {
        failure(e);
        // throw e;
      });
  }

  return ret !== false ? success(ret) : failure(ret);
}

/**
 * 
 * @param {Object} target
 * @return {Boolean} true, false
 */
export function isFunction(target) {
  return typeof target === 'function';
}
