import React, { Component } from 'react';
import classNames from 'classnames';
import PropTypes from 'prop-types';
import { obj } from '../util';
import InnerSlider from './slick/inner-slider';
import ConfigProvider from '../config-provider';

/**
 * Slider
 */
export default class Slider extends Component {
  static propTypes = {
    prefix: PropTypes.string,
    rtl: PropTypes.bool,
    /**
     * 
     */
    className: PropTypes.any,
    /**
     * 
     */
    adaptiveHeight: PropTypes.bool,
    /**
     * 'slide'
     */
    animation: PropTypes.oneOfType([PropTypes.string, PropTypes.bool]),
    /**
     * 
     */
    arrows: PropTypes.bool,
    /**
     *  : 'medium', 'large'
     */
    arrowSize: PropTypes.oneOf(['medium', 'large']),
    /**
     *  : 'inner', 'outer'
     */
    arrowPosition: PropTypes.oneOf(['inner', 'outer']),
    /**
     *  : 'hoz', 'ver'
     */
    arrowDirection: PropTypes.oneOf(['hoz', 'ver']),
    /**
     * 
     */
    autoplay: PropTypes.bool,
    /**
     * 
     */
    autoplaySpeed: PropTypes.number,
    /**
     * 
     */
    nextArrow: PropTypes.element,
    /**
     * 
     */
    prevArrow: PropTypes.element,
    /**
     * 
     */
    centerMode: PropTypes.bool,
    /**
     * 
     */
    dots: PropTypes.bool,
    /**
     * 
     */
    dotsDirection: PropTypes.oneOf(['hoz', 'ver']),
    dotsClass: PropTypes.string,
    /**
     * 
     */
    dotsRender: PropTypes.func,
    /**
     * 
     */
    draggable: PropTypes.bool,
    /**
     * 
     */
    infinite: PropTypes.bool,
    /**
     * 
     */
    defaultActiveIndex: PropTypes.number,
    /**
     * 
     */
    lazyLoad: PropTypes.bool,
    slide: PropTypes.string,
    /**
     * 
     */
    slideDirection: PropTypes.oneOf(['hoz', 'ver']),
    /**
     * 
     */
    slidesToShow: PropTypes.number,
    /**
     * 
     */
    slidesToScroll: PropTypes.number,
    /**
     * 
     */
    speed: PropTypes.number,
    /**
     * 
     */
    activeIndex: PropTypes.number,
    /**
     * 
     */
    triggerType: PropTypes.oneOf(['click', 'hover']),
    /**
     * 
     * @param {Number} index 
     */
    onChange: PropTypes.func,
    onBeforeChange: PropTypes.func, //  0.x onBeforeChange
    children: PropTypes.any,
    /**
     * class
     */
    style: PropTypes.object,
    /**
     * Side padding when in center mode (px or %); centerpading
     */
    centerPadding: PropTypes.string,
    /**
     * CSS3 Animation Easing,ease
     */
    cssEase: PropTypes.string, // used
    edgeFriction: PropTypes.number, // 
    /**
     * 
     */
    focusOnSelect: PropTypes.bool,
    pauseOnHover: PropTypes.bool, // 
    swipe: PropTypes.bool,
    swipeToSlide: PropTypes.bool,
    touchMove: PropTypes.bool,
    touchThreshold: PropTypes.number,
    useCSS: PropTypes.bool,
    variableWidth: PropTypes.bool, // used
    waitForAnimate: PropTypes.bool,
    edgeEvent: PropTypes.any,
    swipeEvent: PropTypes.any,
  };

  static defaultProps = {
    prefix: 'next-',
    animation: 'slide',
    arrowSize: 'medium',
    arrowPosition: 'inner',
    vertical: false,
    verticalSwiping: true,
    dots: true,
    dotsDirection: 'hoz',
    arrows: true,
    arrowDirection: 'hoz',
    infinite: true,
    autoplay: false,
    autoplaySpeed: 3000,
    speed: 500,
    adaptiveHeight: false,
    centerMode: false,
    centerPadding: '50px', // Side padding when in center mode (px or %); centerpading
    cssEase: 'ease',
    draggable: true,
    edgeFriction: 0.35,
    focusOnSelect: false,
    defaultActiveIndex: 0,
    lazyLoad: false,
    pauseOnHover: false,
    rtl: false,
    slide: 'div',
    slideDirection: 'hoz',
    slidesToShow: 1,
    slidesToScroll: 1,
    swipe: true,
    swipeToSlide: false, // Allow users to drag or swipe directly to a slide irrespective of slidesToScroll
    touchMove: true, // touch
    touchThreshold: 5,
    useCSS: true,
    variableWidth: false,
    waitForAnimate: true,
    onChange: () => {},
    onBeforeChange: () => {},
    edgeEvent: null,
    swipeEvent: null,
    nextArrow: null, // nextArrow, prevArrow are react components
    prevArrow: null,
    style: null,
    dotsRender: null,
    triggerType: 'click',
  };

  resize = () => {
    // export api
    this.innerSlider.onWindowResized();
  };

  render() {
    const { prefix, arrowPosition, slideDirection, style, className, children } = this.props;

    const sliderProps = obj.pickOthers(['className', 'style', 'slideDirection'], this.props);
    const slideCount = React.Children.count(children);

    if (slideCount === 0) {
      //  item  slider
      return null;
    } else if (slideCount === 1) {
      //  item 
      sliderProps.arrows = false;
      sliderProps.autoplay = false;
      sliderProps.draggable = false;
    }

    const clazz = classNames(
      [`${prefix}slick`, `${prefix}slick-${arrowPosition}`, `${prefix}slick-${slideDirection}`],
      className,
    );

    if (slideDirection === 'ver') {
      //  vertical 
      sliderProps.vertical = true;
      sliderProps.verticalSwiping = true;
    }

    const newContext = ConfigProvider.getContext();

    return (
      <ConfigProvider {...newContext} rtl={false}>
        <div
          dir="ltr"
          className={clazz}
          style={style}
          {...obj.pickOthers({ ...Slider.propTypes, ...InnerSlider.propTypes }, sliderProps)}
        >
          <InnerSlider ref={InnerSlider => (this.innerSlider = InnerSlider)} {...sliderProps} />
        </div>
      </ConfigProvider>
    );
  }
}
