import React from 'react';
import PropTypes from 'prop-types';
import Dialog from './dialog';
import ConfigProvider from '../config-provider';
import Button from '../button';

const noop = () => {};

class StepDialog extends React.Component {
  static propTypes = {
    prefix: PropTypes.string,
    rtl: PropTypes.bool,
    className: PropTypes.string,
    /**
     * 
     */
    visible: PropTypes.bool,
    /**
     * 
     */
    children: PropTypes.node,
    /**
     * 
     */
    cancelProps: PropTypes.object,
    /**
     * 
     * **close** 
     * **mask** 
     * **esc**  esc 
     *  'close'  'close,esc,mask'
     *  true
     *  false
     */
    closeable: PropTypes.oneOfType([PropTypes.string, PropTypes.bool]),
    /**
     * 
     * @param {String} trigger 
     * @param {Object} event 
     */
    onClose: PropTypes.func,
    /**
     * , 
     */
    afterClose: PropTypes.func,
    /**
     * 
     */
    hasMask: PropTypes.bool,
    /**
     * 
     * @property {String} in 
     * @property {String} out 
     */
    animation: PropTypes.oneOfType([PropTypes.object, PropTypes.bool]),
    /**
     * 
     */
    autoFocus: PropTypes.bool,
    /**
     * , Overlay
     */
    align: PropTypes.oneOfType([PropTypes.string, PropTypes.bool]),
    /**
     *  align  'cc cc' 
     */
    isFullScreen: PropTypes.bool,
    /**
     * 
     */
    shouldUpdatePosition: PropTypes.bool,
    /**
     * align  'cc cc'  isFullScreen  true 
     */
    minMargin: PropTypes.number,
    /**
     * 
     */
    overlayProps: PropTypes.object,
    /**
     * 
     */
    height: PropTypes.string,
    /**
     * padding
     */
    noBodyPadding: PropTypes.bool,
    /**
     * id
     */
    current: PropTypes.string,
    /**
     * id
     */
    defaultCurrent: PropTypes.string,
    /**
     * 
     */
    onStepChange: PropTypes.func,
    /**
     * 
     */
    loading: PropTypes.bool,
    /**
     * 
     */
    loadingTip: PropTypes.node,
  };

  static defaultProps = {
    prefix: 'next-',
    visible: false,
    closeable: 'esc,close',
    onClose: noop,
    afterClose: noop,
    hasMask: true,
    animation: {
      in: 'fadeInDown',
      out: 'fadeOutUp',
    },
    autoFocus: false,
    align: 'cc cc',
    isFullScreen: false,
    shouldUpdatePosition: false,
    minMargin: 40,
    overlayProps: {},
    noBodyPadding: false,
    onStepChange: noop,
    loading: false,
  };

  constructor(props) {
    super(props);
    this.state = {
      current: props.current || props.defaultCurrent,
    };
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    if (nextProps.current !== this.props.current) {
      this.setState({
        current: nextProps.current,
      });
    }
  }

  onStepChange = current => {
    if (!('current' in this.props)) {
      this.setState({
        current,
      });
    }
    this.props.onStepChange(current);
  };

  getViewMeta = () => {
    const { children } = this.props;
    const meta = [];
    React.Children.forEach(children, c => {
      if (c) {
        meta.push({
          id: c.props.id,
          title: c.props.title,
          footer: c.props.footer,
        });
      }
    });
    return meta;
  };

  checkView = () => {
    const { children } = this.props;
    let isView = true;
    if (children === null) {
      return false;
    }
    React.Children.forEach(children, c => {
      if (c && c.type && c.type.displayName !== 'StepView') {
        isView = false;
      }
    });
    return isView;
  };

  render() {
    if (!this.checkView()) {
      return null;
    }
    const { children, prefix } = this.props;
    const metas = this.getViewMeta();
    const { current } = this.state;
    const meta = metas.find(m => m.id === current) || {};
    const index = metas.findIndex(m => m.id === current);
    const child = React.Children.toArray(children).find(c => c.props.id === current);
    const title =
      index === 0 ? (
        <span>{meta.title}</span>
      ) : (
        /* eslint no-script-url: 1 */
        <span>
          <a role="button" href="javascript:;" className={`${prefix}dialog-prev`}>
            <Button.Icon
              name="chevron-left"
              type="primary"
              color="gray"
              onClick={this.onStepChange.bind(this, metas[index - 1].id)}
            />
          </a>
          <span>{meta.title}</span>
        </span>
      );
    return (
      <Dialog {...this.props} title={title} footer={meta.footer}>
        {child}
      </Dialog>
    );
  }
}

export default ConfigProvider.config(StepDialog);
