import React from 'react';
import PropTypes from 'prop-types';
import Overlay from '../overlay';
import BalloonInner from './inner';
import { edgeMap as alignMap } from './alignMap';
import getDisabledCompatibleTrigger from './util';

const { Popup } = Overlay;

/** Balloon.Tooltip */
export default class Tooltip extends React.Component {
  static propTypes = {
    /**
     * 
     */
    prefix: PropTypes.string,
    /**
     * 
     */
    className: PropTypes.string,
    /**
     * 
     */
    style: PropTypes.object,
    /**
     * 
     */
    type: PropTypes.oneOf(['normal', 'primary']),
    /**
     * tooltip
     */
    children: PropTypes.any,
    /**
     * 
     * @enumdesc , , , , , , , , , , ,   
     */
    align: PropTypes.oneOf(['t', 'r', 'b', 'l', 'tl', 'tr', 'bl', 'br', 'lt', 'lb', 'rt', 'rb']),
    /**
     * 
     */
    trigger: PropTypes.any,
    /**
     * 
     * ,  ('hover', 'click') ['hover', 'click'], 'focus'triggerTypeclickBalloon
     */
    triggerType: PropTypes.oneOfType([PropTypes.string, PropTypes.array]),
    /**
     * stylePopup
     */
    popupStyle: PropTypes.object,
    /**
     * classNamePopup
     */
    popupClassName: PropTypes.string,
    /**
     * Popup
     */
    popupProps: PropTypes.object,
    /**
     * pure render
     */
    pure: PropTypes.bool,
    /**
     * , id
     */
    popupContainer: PropTypes.oneOfType([PropTypes.string, PropTypes.func]),
    /**
     * 
     */
    followTrigger: PropTypes.bool,
    /**
     * id, 
     */
    id: PropTypes.string,
  };

  static defaultProps = {
    type: 'primary',
    triggerType: 'hover',
    prefix: 'next-',
    align: 'b',
    trigger: <span />,
  };

  constructor(props) {
    super(props);
    const expectedAlign = alignMap[props.align][props.rlt ? 'rtlAlign' : 'align'];
    this.state = {
      expectedAlign,
    };
  }

  // overylayalignalignMapdirtoolotip align
  getSimpleAlign = (expectedAlign, rtl) => {
    const simpleKeys = Object.keys(alignMap);
    if (rtl) {
      return simpleKeys.find(k => alignMap[k].rtlAlign === expectedAlign);
    }
    return simpleKeys.find(k => alignMap[k].align === expectedAlign);
  }

  onPosition = (...args) => {
    if (this.state.expectedAlign !== args[0].align.join(' ')) {
      const expectedAlign = args[0].align.join(' ');
      this.setState({
        expectedAlign,
      });
      this.props.onPosition && this.props.onPosition(...args);
    }
  }

  render() {
    const {
      id,
      type,
      className,
      align,
      style,
      prefix,
      trigger,
      children,
      popupContainer,
      popupProps,
      popupClassName,
      popupStyle,
      followTrigger,
      triggerType,
      autoFocus,
      rtl,
      ...others
    } = this.props;

    let trOrigin = 'trOrigin';
    if (rtl) {
      others.rtl = true;
      trOrigin = 'rtlTrOrigin';
    }
    // 
    const simpleAlign = this.getSimpleAlign(this.state.expectedAlign, rtl);
    const transformOrigin = alignMap[simpleAlign][trOrigin];
    const _offset = alignMap[simpleAlign].offset;
    const _style = { transformOrigin, ...style };
    const content = (
      <BalloonInner
        {...others}
        id={id}
        prefix={prefix}
        closable={false}
        type={type}
        isTooltip
        className={className}
        style={_style}
        align={simpleAlign}
        rtl={rtl}
      >
        <span className={`${prefix}balloon-tooltip-content`}>{children}</span>
      </BalloonInner>
    );

    const triggerProps = {};
    triggerProps['aria-describedby'] = id;
    triggerProps.tabIndex = '0';

    let newTriggerType = triggerType;

    if (triggerType === 'hover' && id) {
      newTriggerType = ['focus', 'hover'];
    }

    const ariaTrigger = id ? React.cloneElement(trigger, triggerProps) : trigger;

    const newTrigger = getDisabledCompatibleTrigger(
      React.isValidElement(ariaTrigger) ? ariaTrigger : <span>{ariaTrigger}</span>,
    );

    // transform: scalenode.offsetWidth
    // node.getBoundingClientRect()
    // scale
    return (
      <Popup
        role="tooltip"
        container={popupContainer}
        followTrigger={followTrigger}
        trigger={newTrigger}
        triggerType={newTriggerType}
        align={this.state.expectedAlign}
        offset={_offset}
        delay={300}
        className={popupClassName}
        style={popupStyle}
        rtl={rtl}
        autoFocus={triggerType === 'focus' ? false : autoFocus}
        shouldUpdatePosition
        // needAdjust
        {...popupProps}
        onPosition={this.onPosition}
        animation={{
          in: 'fadeIn',
          out: 'fadeOut',
        }}
      >
        {content}
      </Popup>
    );
  }
}
