import React, { Component } from 'react';
import classnames from 'classnames';
import { func } from '../util';

const { makeChain } = func;
// UIState 
// CSScheckboxradio
//  disable 
// disable resetUIState
/* eslint-disable react/prop-types */
class UIState extends Component {
  constructor(props) {
    super(props);
    this.state = {};
    ['_onUIFocus', '_onUIBlur'].forEach(item => {
      this[item] = this[item].bind(this);
    });
  }
  // base 
  getStateElement(base) {
    const { onFocus, onBlur } = this.props;
    return React.cloneElement(base, {
      onFocus: makeChain(this._onUIFocus, onFocus),
      onBlur: makeChain(this._onUIBlur, onBlur),
    });
  }
  // classname
  getStateClassName() {
    const { focused } = this.state;
    return classnames({
      focused,
    });
  }
  // 
  resetUIState() {
    this.setState({
      focused: false,
    });
  }
  _onUIFocus() {
    this.setState({
      focused: true,
    });
  }
  _onUIBlur() {
    this.setState({
      focused: false,
    });
  }
}

export default UIState;
