import React from 'react';
import PropTypes from 'prop-types';
import Overlay from '../overlay';
import { func, obj, log } from '../util';
import BalloonInner from './inner';
import { normalMap, edgeMap } from './alignMap';
import getDisabledCompatibleTrigger from './util';

const { noop } = func;
const { Popup } = Overlay;

const alignList = ['t', 'r', 'b', 'l', 'tl', 'tr', 'bl', 'br', 'lt', 'lb', 'rt', 'rb'];

let alignMap = normalMap;

/** Balloon */
export default class Balloon extends React.Component {
  static displayName = 'Balloon';

  static contextTypes = {
    prefix: PropTypes.string,
  };

  static propTypes = {
    prefix: PropTypes.string,
    pure: PropTypes.bool,
    rtl: PropTypes.bool,
    /**
     * 
     */
    className: PropTypes.string,
    /**
     * 
     */
    style: PropTypes.object,
    /**
     * 
     */
    children: PropTypes.any,
    size: PropTypes.string,
    /**
     * 
     */
    type: PropTypes.oneOf(['normal', 'primary']),
    /**
     * 
     */
    visible: PropTypes.bool,
    /**
     * 
     */
    defaultVisible: PropTypes.bool,
    /**
     * 
     * @param {Boolean} visible 
     * @param {String} type  closeClick  fromTrigger trigger docClick document
     */
    onVisibleChange: PropTypes.func,
    /**
     * 
     */
    alignEdge: PropTypes.bool,
    /**
     * 
     */
    closable: PropTypes.bool,
    /**
     * 
     * @enumdesc , , , , , , , , , , ,   
     */
    align: PropTypes.oneOf(alignList),
    /**
     * trigger, [hoz, ver],  left / top 
     * e.g. [100, 100] (RTL ) 100px
     */
    offset: PropTypes.array,
    /**
     * 
     */
    trigger: PropTypes.any,
    /**
     * 
     * , ('hover','click') ['hover', 'click'], 'focus'click
     */
    triggerType: PropTypes.oneOfType([PropTypes.string, PropTypes.array]),

    onClick: PropTypes.func,
    /**
     * visiblefalse
     */
    onClose: PropTypes.func,
    onHover: PropTypes.func,
    /**
     * 
     */
    needAdjust: PropTypes.bool,
    /**
     * ,  ms
     */
    delay: PropTypes.number,
    /**
     * , 
     */
    afterClose: PropTypes.func,
    /**
     * 
     */
    shouldUpdatePosition: PropTypes.bool,
    /**
     * focus
     */
    autoFocus: PropTypes.bool,
    /**
     * :triggetTypeclick,.safeNode, domiddom
     */
    safeNode: PropTypes.string,
    /**
     * safeNodeidsafeNode
     */
    safeId: PropTypes.string,
    /**
     * 
     * @param {String} in 
     * @param {String} out 
     */
    animation: PropTypes.oneOfType([PropTypes.object, PropTypes.bool]),

    /**
     * dom
     */
    cache: PropTypes.bool,
    /**
     * , id
     */
    popupContainer: PropTypes.oneOfType([PropTypes.string, PropTypes.func]),
    container: PropTypes.oneOfType([PropTypes.string, PropTypes.func]),
    /**
     * stylePopup
     */
    popupStyle: PropTypes.object,
    /**
     * classNamePopup
     */
    popupClassName: PropTypes.string,
    /**
     * Popup
     */
    popupProps: PropTypes.object,
    /**
     * 
     */
    followTrigger: PropTypes.bool,
    /**
     * id, 
     */
    id: PropTypes.string,
  };

  static defaultProps = {
    prefix: 'next-',
    pure: false,
    type: 'normal',
    closable: true,
    defaultVisible: false,
    size: 'medium',
    alignEdge: true,
    align: 'b',
    offset: [0, 0],
    trigger: <span />,
    onClose: noop,
    afterClose: noop,
    onVisibleChange: noop,
    needAdjust: true,
    triggerType: 'hover',
    safeNode: undefined,
    safeId: null,
    autoFocus: true,
    animation: {
      in: 'zoomIn',
      out: 'zoomOut',
    },
    cache: false,
    popupStyle: {},
    popupClassName: '',
    popupProps: {},
  };

  constructor(props, context) {
    super(props, context);
    this.state = {
      align: alignList.includes(props.align) ? props.align : 'b',
      visible: 'visible' in props ? props.visible : props.defaultVisible,
    };
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    if ('visible' in nextProps) {
      this.setState({
        visible: nextProps.visible,
      });
    }

    if ('align' in nextProps && alignList.includes(nextProps.align)) {
      this.setState({
        align: nextProps.align,
      });
    }
  }

  _onVisibleChange = (visible, trigger) => {
    // Not Controlled
    if (!('visible' in this.props)) {
      this.setState({
        visible,
      });
    }

    this.props.onVisibleChange(visible, trigger);

    if (!visible) {
      this.props.onClose();
    }
  };

  _onClose = e => {
    this._onVisibleChange(false, 'closeClick');

    // preventDefault,IE,full page reload 
    e.preventDefault();
  };

  _onPosition = res => {
    const { rtl } = this.props;
    alignMap = this.props.alignEdge ? edgeMap : normalMap;
    const newAlign = res.align.join(' ');
    let resAlign;

    let alignKey = 'align';
    if (rtl) {
      alignKey = 'rtlAlign';
    }

    for (const key in alignMap) {
      if (alignMap[key][alignKey] === newAlign) {
        resAlign = key;

        break;
      }
    }

    resAlign = resAlign || this.state.align;
    if (resAlign !== this.state.align) {
      this.setState({
        align: resAlign,
      });
    }
  };

  render() {
    const {
      id,
      type,
      prefix,
      className,
      alignEdge,
      trigger,
      triggerType,
      children,
      closable,
      shouldUpdatePosition,
      delay,
      needAdjust,
      safeId,
      autoFocus,
      safeNode,
      onClick,
      onHover,
      animation,
      offset,
      style,
      container,
      popupContainer,
      cache,
      popupStyle,
      popupClassName,
      popupProps,
      followTrigger,
      rtl,
      ...others
    } = this.props;

    if (container) {
      log.deprecated('container', 'popupContainer', 'Balloon');
    }

    const { align } = this.state;

    alignMap = alignEdge ? edgeMap : normalMap;
    const _prefix = this.context.prefix || prefix;

    let trOrigin = 'trOrigin';
    if (rtl) {
      trOrigin = 'rtlTrOrigin';
    }

    const _offset = [alignMap[align].offset[0] + offset[0], alignMap[align].offset[1] + offset[1]];
    const transformOrigin = alignMap[align][trOrigin];
    const _style = { ...{ transformOrigin }, ...style };

    const content = (
      <BalloonInner
        {...obj.pickOthers(Object.keys(Balloon.propTypes), others)}
        id={id}
        prefix={_prefix}
        closable={closable}
        onClose={this._onClose}
        className={className}
        style={_style}
        align={align}
        type={type}
        rtl={rtl}
        alignEdge={alignEdge}
      >
        {children}
      </BalloonInner>
    );

    const triggerProps = {};
    triggerProps['aria-describedby'] = id;
    triggerProps.tabIndex = '0';

    const ariaTrigger = id ? React.cloneElement(trigger, triggerProps) : trigger;

    const newTrigger = getDisabledCompatibleTrigger(
      React.isValidElement(ariaTrigger) ? ariaTrigger : <span>{ariaTrigger}</span>,
    );

    return (
      <Popup
        {...popupProps}
        followTrigger={followTrigger}
        trigger={newTrigger}
        cache={cache}
        safeId={safeId}
        triggerType={triggerType}
        align={alignMap[align].align}
        offset={_offset}
        visible={this.state.visible}
        onPosition={this._onPosition}
        onClick={onClick}
        onHover={onHover}
        afterClose={this.props.afterClose}
        onVisibleChange={this._onVisibleChange}
        shouldUpdatePosition={shouldUpdatePosition}
        needAdjust={needAdjust}
        animation={animation}
        delay={delay}
        autoFocus={triggerType === 'focus' ? false : autoFocus}
        safeNode={safeNode}
        container={popupContainer || container}
        className={popupClassName}
        style={popupStyle}
        rtl={rtl}
      >
        {content}
      </Popup>
    );
  }
}
